.transformations {
  text-align: center;
  padding: 5rem 1rem;

  h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }

  .transformation-slider {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .transformation-item {
      display: flex;
      justify-content: space-between;
      max-width: 800px;
      margin: 0 1rem;

      .transformation-before,
      .transformation-after {
        width: 48%;
        text-align: center;

        h3 {
          margin-bottom: 0.5rem;
        }

        .transformation-image {
          width: 100%;
          height: auto;
        }
      }
    }

    .prev-btn, .next-btn {
      background: none;
      border: none;
      font-size: 2rem;
      cursor: pointer;
      padding: 0 1rem;
      color: #354d40;
      transition: color 0.3s;

      &:hover {
        color: #aad2ba;
      }
    }
  }

  .transformation-dots {
    margin-top: 1rem;

    .dot {
      display: inline-block;
      width: 10px;
      height: 10px;
      margin: 0 5px;
      background-color: #ddd;
      border-radius: 50%;
      cursor: pointer;

      &.active {
        background-color: #354d40;
      }
    }
  }
}
